@import url(https://fonts.googleapis.com/css2?family=Rubik&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.header{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 30px;
    height: 80px;
    color: Black;
    background: rgb(238,174,202);
    background: linear-gradient(56deg, rgba(238,174,202,1) 0%, rgba(148,187,233,1) 100%);
}

header h1{
    font-size: 30px;
    color: black;
}

.App{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    color: #1d1d1d;
    min-height: 100vh;
    background-color: #070707;
}

.News-header{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 50px;
    margin: 20px;
}
.News-header h1{
    font-size: 40px;
}
.news-container{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    flex-grow: 1;
}
.coin-tracker{
    margin-top: 100px;
    padding-top: 50px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 95%;
    border-radius: 20px;
    background: rgb(238,174,202);
    background: linear-gradient(56deg, rgb(92, 4, 42) 0%, rgb(6, 39, 77) 100%);
}

.heading{
    margin: 20px;
    font-size: 30px;
}

.coin-search{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
}
.coin-input{
    height: 40px;
    text-align: center;
    border-radius: 10px;
    outline: none;
    border: 2px solid rgb(182, 182, 182);
    margin-left: 10px;
    width: 100%;
    color: black;
    box-shadow: -3px 2px 30px -13px rgba(18,18,18,1);
}
.form{
    width: 50%;
}

.git-btn{
    padding: 10px;
    color: black;
    font-family: monospace;
    border-radius: 5px;
    border: none;
    outline: 0;
    background-color: white ;
    text-decoration: none;
}
.git-btn:hover{
    background-color: rgba(238,174,202,1);
    cursor: pointer;
}
.logo{
    display: flex;
    align-items: center;
    align-self: center;
    justify-content: center;
    margin-right: 20px;
}

.logo{
    -webkit-filter: grayscale(100%);
            filter: grayscale(100%)
}

@media only screen and (max-width: 800px) {
    .coin-search{
        display: flex;
        align-items: center;
        justify-content: center;
        width: 80%;
        margin: 50px;
    }
    .coin-search h1{
        font-size: 30px;
    }
    .coin-input{
        height: 40px;
        text-align: center;
        border-radius: 10px;
        outline: none;
        border: 1px solid #1d1d1d;
        margin-left: 10px;
        width: 100%;
    }
    .form{
        width: 100%;
    }
  }
.coin-container {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    color: white;
  }
  
  .coin-row {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    height: 80px;
    border-bottom: 1px solid #d7d7d7;
    width: 80%;
  }
  
  .coin {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-right: 24px;
    width: 30%;
  }
  .coin-logo{
    width: 35px;
    height: 35px;
  }
  .coin h1 {
    font-size: 16px;
    width: 150px;
  }
  
  .coin img {
    height: 30px;
    width: 30px;
    margin-right: 10px;
  }
  
  .coin-symbol {
    text-transform: uppercase;
    margin-left: 20px;
    width: 50px;
  }
  
  .coin-data {
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    width: 70%;
    text-align: right;
  }
  
  .coin-price {
    width: 80px;
  }
  
  .coin-volume {
    width: 100px;
  }
  
  .coin-marketcap {
    width: 220px;
    display: flex;
    justify-content: left;
  }
  
  .coin-percent {
    width: 100px;
  }
  
  .red {
    color: #f00606;
  }
  
  .green {
    color: #11d811;
  }
  @media only screen and (max-width: 1000px) {
    .coin-container {
      display: flex;
      justify-content: center;
      width: 100%;
    }
    .coin-row {
      display: flex;
      flex-direction: row;
      justify-content: flex-start;
      align-items: center;
      height: 80px;
      border-bottom: 1px solid #d7d7d7;
      width: 90%;
    }
    .coin-marketcap{
      display: none;
    }
    .coin-data {
      display: flex;
      align-items: flex-end;
      justify-content: space-around;
      width: 60%;
      text-align: right;
    }
    .coin {
      display: flex;
      text-align: left;
      align-items: center;
      justify-content:space-evenly;
      padding-right: 24px;
      width: 40%;
    }

  }

@media only screen and (max-width: 600px) {
  .coin-container {
    display: flex;
    justify-content: center;
    width: 100%;
  }
  .coin-row {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    height: 80px;
    border-bottom: 1px solid #d7d7d7;
    width: 90%;
  }
    .coin-name{
      display: none;
    }
    .coin-volume{
        display: none;
    }
    .coin-marketcap{
      display: none;
    }
    .coin-data {
      display: flex;
      align-items: flex-end;
      justify-content: space-around;
      width: 60%;
      text-align: right;
    }
  }
.container {
    margin-top: 100px;
    display: flex !important;
    flex-direction: row !important;
    width: 300px;
    margin: 20px;
    box-shadow: 0 3px 10px rgb(0 0 0 / 0.2);
    min-height: 500px;
    justify-content: space-between;
    border-radius: 10px;
    align-items: center;
    background-color: #1d1d1d;
    color: white;
  }

.news-row{
    display: flex !important;
    flex-direction: column !important;
    min-height: 400px;
    width: 300px;
    padding: 10px;
    justify-content: space-between;
}

.news-img{
    border-radius: 10px;
}


.news-main{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

h1{
    color: rgb(243, 243, 243);
    font-size: 18px;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
}

.news-description{
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 6; /* number of lines to show */
    -webkit-box-orient: vertical;
}
.news-row p{  
    margin-top: 5px;
    font-size: 15px;
    font-family: sans-serif;
}

a{
    font-size: 12px;
    text-decoration: none;
    margin-top: 5px ;
    color: rgba(148,187,233,1);
}
