@import url('https://fonts.googleapis.com/css2?family=Rubik&display=swap');
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.header{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 30px;
    height: 80px;
    color: Black;
    background: rgb(238,174,202);
    background: linear-gradient(56deg, rgba(238,174,202,1) 0%, rgba(148,187,233,1) 100%);
}

header h1{
    font-size: 30px;
    color: black;
}

.App{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    color: #1d1d1d;
    min-height: 100vh;
    background-color: #070707;
}

.News-header{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 50px;
    margin: 20px;
}
.News-header h1{
    font-size: 40px;
}
.news-container{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    flex-grow: 1;
}
.coin-tracker{
    margin-top: 100px;
    padding-top: 50px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 95%;
    border-radius: 20px;
    background: rgb(238,174,202);
    background: linear-gradient(56deg, rgb(92, 4, 42) 0%, rgb(6, 39, 77) 100%);
}

.heading{
    margin: 20px;
    font-size: 30px;
}

.coin-search{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
}
.coin-input{
    height: 40px;
    text-align: center;
    border-radius: 10px;
    outline: none;
    border: 2px solid rgb(182, 182, 182);
    margin-left: 10px;
    width: 100%;
    color: black;
    -webkit-box-shadow: -3px 2px 30px -13px rgba(18,18,18,1);
    -moz-box-shadow: -3px 2px 30px -13px rgba(18,18,18,1);
    box-shadow: -3px 2px 30px -13px rgba(18,18,18,1);
}
.form{
    width: 50%;
}

.git-btn{
    padding: 10px;
    color: black;
    font-family: monospace;
    border-radius: 5px;
    border: none;
    outline: 0;
    background-color: white ;
    text-decoration: none;
}
.git-btn:hover{
    background-color: rgba(238,174,202,1);
    cursor: pointer;
}
.logo{
    display: flex;
    align-items: center;
    align-self: center;
    justify-content: center;
    margin-right: 20px;
}

.logo{
    filter: grayscale(100%)
}

@media only screen and (max-width: 800px) {
    .coin-search{
        display: flex;
        align-items: center;
        justify-content: center;
        width: 80%;
        margin: 50px;
    }
    .coin-search h1{
        font-size: 30px;
    }
    .coin-input{
        height: 40px;
        text-align: center;
        border-radius: 10px;
        outline: none;
        border: 1px solid #1d1d1d;
        margin-left: 10px;
        width: 100%;
    }
    .form{
        width: 100%;
    }
  }